import React from "react";
import { Button } from "@mui/material";
import { useCallback } from "react";
import { dialogStore } from "./state";
var downloadFile = function (content, fileName) {
    var a = document.createElement("a");
    var file = new Blob([content], { type: "text/plain" });
    a.href = URL.createObjectURL(file);
    a.download = fileName;
    a.click();
};
export var DialogSaveButton = function () {
    var save = useCallback(function () {
        var lexicon = JSON.stringify(dialogStore.lexicon, null, 2);
        downloadFile(lexicon, "dialog.json");
        var json = JSON.stringify(dialogStore.json, null, 2);
        downloadFile(json, "data.json");
    }, []);
    return (React.createElement("div", { style: { position: "fixed", right: 10, bottom: 10 } },
        React.createElement(Button, { variant: "contained", onClick: save }, "Save")));
};
